import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { Account } from '../entities/Account';
import { AccountTypeCode } from '../enums/AccountTypeCode';
import { AccountStatusCode } from '../enums/AccountStatusCode';
import { Coordinate } from '../common/Coordinate';


/**
 * List of account related actions for system administrator only
 * @RequestHeader X-API-KEY The key to identify the application (console)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class SysAccountsService {

  // URL to web api
  private baseUrl = '/sys/accounts';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Create new account
   * @Return: EntityResponse<Account>
   */
  create(body?: Account) {
    return this.rest.post(`${this.baseUrl}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update existing account in the system
   * @Return: EntityResponse<Account>
   */
  update(id?: string, body?: Account) {
    return this.rest.put(`${this.baseUrl}/${id}`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Update account name
   * @Return: EntityResponse<Account>
   */
  changeName(id?: string, body?: string) {
    return this.rest.put(`${this.baseUrl}/${id}/name`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change account type
   * @Return: EntityResponse<User>
   */
  changeType(id?: string, type?: AccountTypeCode) {
    return this.rest.put(`${this.baseUrl}/${id}/type/${type}`, null);
  }

  /**
   * Change account status
   * @Return: EntityResponse<Account>
   */
  changeStatus(id?: string, status?: AccountStatusCode) {
    return this.rest.put(`${this.baseUrl}/${id}/status/${status}`, null);
  }

  /**
   * Change account groups
   * @Return: EntityResponse<Account>
   */
  changeGroups(id?: string, groupId?: string[]) {
    const params = new Array();		
    if (groupId != null) { params.push(`groupId=${groupId}`); }

		return this.rest.put(`${this.baseUrl}/${id}/groups/{groups}`, null, ...params);
  }

  /**
   * Change account geo-location (center)
   * @Return: EntityResponse<Account> The changed account
   */
  changeGeoLocation(id?: string, body?: Coordinate) {
    return this.rest.put(`${this.baseUrl}/${id}/geoLocation`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Change account geo area
   * @Return: EntityResponse<Account> The changed account
   */
  changeGeoArea(id?: string, body?: Coordinate[]) {
    return this.rest.put(`${this.baseUrl}/${id}/geoArea`, typeof body === 'object' ? JSON.stringify(body) : body);
  }

  /**
   * Get default geo location
   * @Return: EntityResponse<Coordinate> The account location
   */
  getDefaultLocation(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}/geoLocation`);
  }

  /**
   * Delete account from the system
   * The account is moved to DELETED mode and will be deleted after 90 days
   * Only account marked as SUSPENDED can be deleted
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Delete account immediately without account status restrictions
   * @Return: ActionResponse
   */
  purge(id?: string) {
    return this.rest.delete(`${this.baseUrl}/purge/${id}`);
  }

  /**
   * Get single account by id
   * @Return: EntityResponse<Account>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Get accounts by list of ids
   * @Return: EntitiesResponse<Account>
   */
  list(id?: string[]) {
    const params = new Array();		
    if (id != null) { params.push(`id=${id}`); }

		return this.rest.get(`${this.baseUrl}/list`, ...params);
  }

  /**
   * Find list of accounts and filter
   * @Return: QueryResponse<Account>
   */
  find(search?: string, type?: AccountTypeCode[], status?: AccountStatusCode[], sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (search != null) { params.push(`search=${search}`); }
    if (type != null) { params.push(`type=${type}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Get account types statistics for all accounts in the system
   * @Return: EntitiesResponse<AccountTypeSummary>
   */
  getAccountTypeStatistics() {
    return this.rest.get(`${this.baseUrl}/statistics/type`);
  }

}
