import { AbsoluteTimeFrame } from '../common/AbsoluteTimeFrame';
import { BaseEntity } from '../entities/BaseEntity';

/* 
   Calendar type specifies list of events or holidays 
*/
export class Calendar extends BaseEntity {
 
    // Account Id 
    public accountId: string;
 
    // Folder Id (use empty string to associate the calendar to the account level) 
    public folderId: string;
 
    // Calendar name 
    public name: string;
 
    // Calendar description 
    public description: string;
 
    // Is this calendar is enabled 
    public enabled: boolean;
 
    // List of time frames 
    public timeFrames: AbsoluteTimeFrame[];
 
    // Entity Id 
    public id: string;
 
    // Entity document type (system field) 
    public _type: string;
 
    // When the entity was created [Epoch milliseconds Timestamp] 
    public createdOn: number;
 
    // When the entity was updated [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 

}


