import { SensorStatusMask } from '../enums/SensorStatusMask';
import { SensorStatusCode } from '../enums/SensorStatusCode';
import { WebSocketMessageHeader } from '../messages/WebSocketMessageHeader';

/* 
   @WebSocketMessage
   This message is pushed to the client upon any sensor health status change in the registered account 
*/
export class SocketSensorStatusNotification extends WebSocketMessageHeader {
 
    // Error code (0 for success) 
    public code: number;
 
    // Error message (if error occurred) 
    public error: string;
 
    // Status update time [Epoch milliseconds Timestamp] 
    public updatedOn: number;
 
    // Sensor Id 
    public sensorId: string;
 
    // Updated status bit-mask 
    public mask: SensorStatusMask;
 
    // Updated status 
    public status: SensorStatusCode;
 
    // 
    public op: number;
 
    // 
    public ver: number;
 
    // 
    public id: number;
 
    // 
    public cid: number;
 
    // 
    public sid: string;
 

}


