/* 
   Sensor status code 
*/
export enum SensorStatusCode {
 
    // Undefined [0] 
    UNDEFINED = 0,
 
    // Sensor is running [1] 
    ACTIVE = 1,
 
    // Sensor in Warning state [2] 
    WARNING = 2,
 
    // Sensor in Error state [3] 
    ERROR = 3,
 
    // Sensor is not active [4] 
    INACTIVE = 4,
 
    // Suspended (banned) sensor [5] 
    SUSPENDED = 5,
 
    // Sensor was added but did not establish connection yet [6] 
    PENDING = 6,
 
}