import { Injectable, Inject } from '@angular/core';
import { RestUtil } from '../../utils/rest-util';
import { CoreConfig } from '../../config';

import { ApplianceStatusCode } from '../enums/ApplianceStatusCode';
import { CommandStatusCode } from '../enums/CommandStatusCode';


/**
 * List of appliance queries for system administrator only
 * @RequestHeader X-API-KEY The key to identify the application (console)
 * @RequestHeader X-ACCESS-TOKEN The token to identify the logged-in user 
 */
@Injectable()
export class SysAppliancesService {

  // URL to web api
  private baseUrl = '/sys/appliances';

  /**
   * Class constructor
   */
  constructor(@Inject('config') private config: CoreConfig, private rest: RestUtil) {
    this.baseUrl = this.config.api + this.baseUrl;
  }


  /**
   * Get single appliance by id
   * @Return: EntityResponse<Appliance>
   */
  get(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}`);
  }

  /**
   * Find list of appliances and filter
   * @Return: QueryResponse<Appliance>
   */
  find(accountId?: string, search?: string, status?: ApplianceStatusCode[], sort?: string, page?: number, pageSize?: number) {
    const params = new Array();		
    if (accountId != null) { params.push(`accountId=${accountId}`); }
    if (search != null) { params.push(`search=${search}`); }
    if (status != null) { params.push(`status=${status}`); }
    if (sort != null) { params.push(`sort=${sort}`); }
    if (page != null) { params.push(`page=${page}`); }
    if (pageSize != null) { params.push(`pageSize=${pageSize}`); }

		return this.rest.get(`${this.baseUrl}`, ...params);
  }

  /**
   * Delete appliance from the system
   * @Return: ActionResponse
   */
  delete(id?: string) {
    return this.rest.delete(`${this.baseUrl}/${id}`);
  }

  /**
   * Get all appliance agents
   * @Return: EntitiesResponse<Agent>
   */
  getApplianceAgents(id?: string) {
    return this.rest.get(`${this.baseUrl}/${id}/agents`);
  }

  /**
   * Find list of appliance commands and filter
   * @Return: EntitiesResponse<ApplianceCommand> List of appliance commands
   */
  getCommands(accountId?: string, applianceId?: string, status?: CommandStatusCode[]) {
    const params = new Array();		
    if (accountId != null) { params.push(`accountId=${accountId}`); }
    if (applianceId != null) { params.push(`applianceId=${applianceId}`); }
    if (status != null) { params.push(`status=${status}`); }

		return this.rest.get(`${this.baseUrl}/commands`, ...params);
  }

  /**
   * Update command status
   * @Return: ActionResponse
   */
  setCommandStatus(commandId?: string, status?: CommandStatusCode) {
    return this.rest.put(`${this.baseUrl}/commands/${commandId}/${status}`, null);
  }

  /**
   * Delete command
   * @Return: ActionResponse
   */
  deleteCommand(commandId?: string) {
    return this.rest.delete(`${this.baseUrl}/commands/${commandId}`);
  }

}
