import { SensorStatusMask } from '../enums/SensorStatusMask';

/* 
   Data point in a series representing a sensor status value in a specific timestamp 
*/
export class SensorStatusTimestamped {
 
    // Status mask 
    public mask: SensorStatusMask;
 
    // Timestamp (Epoch milliseconds) 
    public timestamp: number;
 
    // Data point label 
    public label: string;
 
    constructor(mask?: SensorStatusMask, timestamp?: number, label?: string) { 
        this.mask = mask;
        this.timestamp = timestamp;
        this.label = label;
    }

}


