import { MapClientCode } from '../enums/MapClientCode';

/* 
   Client configuration parameters (for the UI) 
*/
export class InnoviClientConfig {
 
    // Map Client library to use in the UI 
    public mapClient: MapClientCode;
 
    // Map Client key 
    public mapKey: string;
 
    // Web map service uri 
    public wMS: string;
 
    constructor(mapClient?: MapClientCode, mapKey?: string, wMS?: string) { 
        this.mapClient = mapClient;
        this.mapKey = mapKey;
        this.wMS = wMS;
    }

}


